@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 100%;

  display: flex;
  justify-content: space-between;
  gap: 16px;

  padding: 8px 12px;

  background-color: $white;

  transition: all 0.1s ease-in-out;
  will-change: background-color, height;
  cursor: pointer;

  &:hover {
    background-color: $light-gray;
  }

  @include darkTheme {
    background: $dark-bg-1;
    &:hover {
      background: $dark-bg-2;
    }
  }
}

.main {
  max-width: 50%;

  font-size: 12px;
  line-height: 16px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.additional {
  max-width: 60%;
  width: 100%;
  display: flex;
  flex-direction: column;

  animation-name: fadeIn;
  animation-duration: 0.2s;
  animation-fill-mode: forwards;
}

.additionalData {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}

.additionalDataContainer {
  position: relative;

  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-start;

  font-size: 12px;
  line-height: 16px;

  &:not(:first-child) {
    margin-top: 5px;
  }
}

.inn {
  color: $blue-grey;

  @include darkTheme {
    color: $light-gray;
  }
}

.name {
  color: $blue-grey;

  @include darkTheme {
    color: $light-gray;
  }
}

.dataName {
  width: 60%;
  color: $blue-grey;

  @include darkTheme {
    color: $light-gray;
  }
}

.dataValue {
  max-width: 40%;
  color: $blue-grey;

  @include darkTheme {
    color: $light-gray;
  }
}

.additionalButtonContainer {
  margin: 6px 0 4px;
  display: flex;
  align-items: center;
}

.additionalButtonLabel {
  font-size: 12px;
  line-height: 16px;
  color: $dark-blue;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.additionalInfoIcon {
  margin-left: 6px;
  height: 9px;
  width: 9px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
