@import 'utils/variables', 'utils/mixins';

.container {
  width: 560px;
  padding: 16px 24px;
}

.loader {
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.search {
  position: relative;
  z-index: 1;
}

.list {
  border: 1px solid $base-gray;
  border-radius: 2px;

  margin-top: -2px;
  padding-top: 2px;

  @include darkTheme {
    border-color: $dark-bg-4;
  }
}

.item {
  &:not(:last-child) {
    border-bottom: 1px solid $base-gray;

    @include darkTheme {
      border-color: $dark-bg-4;
    }
  }
}

.loader {
  margin-top: 20px;

  opacity: 0.5;

  display: flex;
  align-items: center;
  justify-content: center;
}

.emptyContainer {
  width: 100%;
  height: 50px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.emptyText {
  font-size: 13px;
  line-height: 16px;
  color: #565656;

  @include darkTheme {
    color: $white;
  }
}

.error {
  width: 100%;
  height: 50px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.errorText {
  font-size: 13px;
  line-height: 16px;
  color: #565656;

  @include darkTheme {
    color: $white;
  }
}

.actions {
  margin-top: 30px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.action {
  width: 175px;
  height: 45px;

  @include respond-to('medium') {
    width: 160px;
    height: 30px;
  }
}

.action span {
  font-size: 14px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.action button {
  width: 100%;
  height: 100%;
}
